<template>
  <section class="pc-container pb-4">
    <div class="pcoded-content">
      <div class="row mb-4 d-flex align-items-center">
        <div class="col-sm-12 col-md-6 col-xl-9">
          <h5><span class="text-primary">Personas</span> | <span class="text-secondary"> {{ tipoSeleccionado === '' ?
            'Lista' : tipoSeleccionado.name }}</span></h5>
        </div>
        <div class="col-sm-12 col-md-6 col-xl-3">
          <select class="form-control" v-model="tipoSeleccionado">
            <option value="" selected disabled>Parte procesal</option>
            <option v-for="item in tipos" :value="item">{{ item.name }}</option>
          </select>
        </div>

      </div>
      <div class="row">
        <div class="col-xl-12">
          <div class="card">
            <!-- <div class="card-header">
                <h5 class="text-secondary">Lista de Demandados</h5>
              </div> -->
            <div class="card-body">
              <div class="table-responsive">
                <table class="table" id="table" ref="myDataTable">
                  <thead>
                    <tr>
                      <th>#</th>
                      <th>Documento</th>
                      <th>Tipo Procesal</th>
                      <th>Condicion</th>
                      <th>Apellido paterno /<br> Razon social</th>
                      <th>Apellido materno</th>
                      <th>Nombres</th>
                      <th>Interacciones</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="(persona, index) in personasFiltradas" :key="index">
                      <td>{{ index + 1 }}</td>
                      <td>{{ documento(persona) }}</td>
                      <td>{{ persona.tipo_procesal }}</td>
                      <td>{{ persona.per_condicion }}</td>
                      <td>{{ apellidoPaternoRazonSocial(persona) }}</td>
                      <td>{{ apellidoMaterno(persona) }}</td>
                      <td>{{ nombres(persona) }}</td>
                      <td>
                        <div class="d-flex gap-2">
                          <button @click="registrarHistorial(persona.documento)" class="btn btn-sm btn-outline-success rounded border-1" style="font-size: 12px;">
                            Registrar Historial
                          </button>

                          <button @click="verDatos(persona.id)" class="btn btn-sm btn-outline-primary rounded border-1" style="font-size: 12px;">
                            Ver datos
                          </button>
                        </div>
                      </td>

                      <!-- <td class="text-center py-2">
                        <div class="d-flex">
                          <div class="tooltip-container">
                            <router-link :to="{ name: 'demandante/historial/nuevo', params: { doc: persona.documento } }"
                              class="tooltip-trigger">
                              <i data-feather="edit"></i>
                            </router-link>
                            <div class="custom-tooltip">Registrar historial</div>
                          </div>
                          <div class="tooltip-container">
                            <router-link :to="{ name: 'demandante/informacion', params: { doc: persona.id } }"
                              class="tooltip-trigger">
                              <i data-feather="file-text"></i>
                            </router-link>
                            <div class="custom-tooltip">Datos del demandante</div>
                          </div>
                          <a href="javascript:void(0)" class="btn btn-danger py-1 px-2 mx-2"
                            @click="editarDni(persona.id)">
                            <i data-feather="credit-card"></i>
                          </a>
                        </div>
                      </td> -->
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>
  
<script>
import feather from 'feather-icons';
import DataTable from '@/utils/dataTable';

export default {
  name: 'demandados',
  mixins: [DataTable],
  mounted() {
    this.leer();
  },
  updated() {
    feather.replace();
  },
  data() {
    return {
      personas: [],
      dataTable: null,
      tipoSeleccionado: '',
      tipos: [
        { id: 1, name: 'Demandante' },
        { id: 2, name: 'Demandado' },
        { id: 3, name: 'Denunciante' },
        { id: 4, name: 'Denunciado' },
      ]
    };
  },
  computed: {
    personasFiltradas(){
      return this.tipoSeleccionado
        ? this.personas.filter(persona => persona.tipo_procesal === this.tipoSeleccionado.name)
        : this.personas;
    }
    // personas() {
    //   const personasMap = new Map();

    //   this.demandados.forEach((demandado) => {
    //     const key = demandado.nat_dni == null
    //       ?
    //       demandado.jur_ruc :
    //       demandado.nat_dni
    //       ;

    //     if (!personasMap.has(key)) {
    //       personasMap.set(key, {
    //         documento: key,
    //         id: demandado.per_id,
    //         nombre: demandado.jur_ruc === null
    //           ? `${demandado.nat_apellido_paterno} ${demandado.nat_apellido_materno} ${demandado.nat_nombres}`
    //           : demandado.jur_razon_social,
    //         telefono: demandado.jur_ruc === null
    //           ? demandado.nat_telefono
    //           : demandado.jur_telefono,
    //         correo: demandado.jur_ruc === null
    //           ? demandado.nat_correo
    //           : demandado.jur_correo,
    //       });
    //     }
    //   });

    //   return Array.from(personasMap.values());
    //},
  },
  methods: {
    documento(persona) {
      return persona.nat_dni === null ? persona.jur_ruc : persona.nat_dni;
    },

    apellidoPaternoRazonSocial(persona) {
      return persona.nat_dni != null ? persona.nat_apellido_paterno : persona.jur_razon_social;
    },

    apellidoMaterno(persona) {
      return persona.nat_dni != null ? persona.nat_apellido_materno : '--';
    },

    nombres(persona) {
      return persona.nat_dni != null ? persona.nat_nombres : '--';
    },

    registrarHistorial(documento) {
      this.$router.push({ name: 'demandante/historial/nuevo', params: { doc: documento } });
    },
    
    verDatos(id) {
      this.$router.push({ name: 'demandante/informacion', params: { doc: id } });
    },

    async leer() {
      try {
        this.$showLoadingAlert();
        const result = await this.$getData('personas', this);
        this.personas = result.data;
        await this.$waitForDataTableDraw(this, 'myDataTable');
        this.$closeLoadingAlert();
      } catch (error) {
        this.$handleApiError(error, 'leer');
      }
    },

  },

};
</script>
  
<style scoped>
@import '@/assets/css/dataTable.css';
@media (min-width: 768px) {
  .table-responsive {
    overflow: hidden !important;
  }
}

.tooltip-container {
  position: relative;
}

.tooltip-trigger {
  cursor: pointer;
  padding: 7.5px;
  border: none;
  background-color: #7267EF;
  color: #fff;
  position: relative;
  border-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 0.55rem;
}

.custom-tooltip {
  width: 10rem;
  position: absolute;
  background-color: #6c757d;
  color: #fff;
  padding: 8px;
  border-radius: 4px;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0s, opacity 0.3s linear;
  z-index: 1;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

.tooltip-trigger:hover+.custom-tooltip {
  visibility: visible;
  opacity: 1;
  top: 50%;
  right: calc(100% - 5px);
  transform: translate(0, -50%);
}

.custom-tooltip::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 100%;
  margin-top: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: transparent transparent transparent #6c757d;
}
</style>